import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TableContainer,
  Paper,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import ConnectMQTT from "../../MQTT/mqtt.js";
import { useSelector } from "react-redux";
function Donnees() {
  const readings = useSelector(state => state.readings);
  const charge_Profiles = useSelector(state => state.charge_Profiles);
  const source_Profiles = useSelector(state => state.source_Profiles);
  useEffect(() => {
    console.log(readings);
  }, [readings]);
  const DEVICES = {
    1: "Panneau Solaire",
    2: "Batterie",
    3: "Onduleur",
    4: "Contrôleur de charge",
    5: "Selecteur de source",
    6: "Chargeur",
    7: "Sortie AC #1",
    8: "Sortie AC #2",
  };

  return (
    <React.Fragment>
      <ConnectMQTT />
      <h1 style={{ padding: "30px 10px 10px 10px" }}>Readings</h1>
      <TableContainer component={Paper}>
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Timestamp</TableCell>
              <TableCell align="right">Device ID</TableCell>
              <TableCell align="right">Current</TableCell>
              <TableCell align="right">Voltage</TableCell>
              <TableCell align="right">Power</TableCell>
              <TableCell align="right">Switch state</TableCell>
              <TableCell align="right">Input/Output</TableCell>
              <TableCell align="right">State of Charge</TableCell>
              <TableCell align="right">Efficiency</TableCell>
              <TableCell align="right">Total Energy Seen</TableCell>
              <TableCell align="right">Total Energy Balance</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {readings
              ? readings.map((reading, index) => (
                  <TableRow key={reading.timestamp + index}>
                    <TableCell component="th" scope="row">
                      {reading.timestamp}
                    </TableCell>
                    <TableCell align="right">
                      {DEVICES[reading.values.Device_ID] || ""}
                    </TableCell>
                    <TableCell align="right">
                      {reading.values.Current}
                    </TableCell>
                    <TableCell align="right">
                      {reading.values.Voltage}
                    </TableCell>
                    <TableCell align="right">{reading.values.Power}</TableCell>
                    <TableCell align="right">
                      {reading.values.Switch_State === true
                        ? "ON"
                        : reading.values.Switch_State === false
                        ? "OFF"
                        : reading.values.Switch_State}
                    </TableCell>
                    <TableCell align="right">
                      {reading.values.Input_Output === true
                        ? "INPUT"
                        : reading.values.Input_Output === false
                        ? "OUTPUT"
                        : reading.values.Input_Output}
                    </TableCell>
                    <TableCell align="right">{reading.values.SoC}</TableCell>
                    <TableCell align="right">
                      {reading.values.Efficiency}
                    </TableCell>
                    <TableCell align="right">
                      {reading.values.Total_Energy_Seen}
                    </TableCell>
                    <TableCell align="right">
                      {reading.values.Total_Energy_Balance}
                    </TableCell>
                  </TableRow>
                ))
              : null}
          </TableBody>
        </Table>
      </TableContainer>
      <h1 style={{ padding: "30px 10px 10px 10px" }}>Source Profil</h1>
      <TableContainer component={Paper}>
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Timestamp</TableCell>
              <TableCell align="right">Device ID</TableCell>
              <TableCell align="right">Température</TableCell>
              <TableCell align="right">Azimuth</TableCell>
              <TableCell align="right">Zénith</TableCell>
              <TableCell align="right">Wind Speed</TableCell>
              <TableCell align="right">Wind direction</TableCell>
              <TableCell align="right">Cloudiness</TableCell>
              <TableCell align="right">Generated Power</TableCell>
              <TableCell align="right">State of Charge</TableCell>
              <TableCell align="right">Real forecast</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {source_Profiles
              ? source_Profiles.map((source_Profile, index) => (
                  <TableRow key={source_Profile.timestamp + index}>
                    <TableCell component="th" scope="row">
                      {source_Profile.timestamp}
                    </TableCell>
                    <TableCell align="right">
                      {source_Profile.values.Device_ID}
                    </TableCell>
                    <TableCell align="right">
                      {source_Profile.values.Temperature}
                    </TableCell>
                    <TableCell align="right">
                      {source_Profile.values.Azimuth}
                    </TableCell>
                    <TableCell align="right">
                      {source_Profile.values.Zenith}
                    </TableCell>
                    <TableCell align="right">
                      {source_Profile.values.Wind_Speed}
                    </TableCell>
                    <TableCell align="right">
                      {source_Profile.values.Wind_Direction}
                    </TableCell>
                    <TableCell align="right">
                      {source_Profile.values.Cloudiness}
                    </TableCell>
                    <TableCell align="right">
                      {source_Profile.values.Generated_Power}
                    </TableCell>
                    <TableCell align="right">
                      {source_Profile.values.SoC}
                    </TableCell>
                    <TableCell align="right">
                      {source_Profile.values.Real_Forecast ? "True" : "False"}
                    </TableCell>
                  </TableRow>
                ))
              : null}
          </TableBody>
        </Table>
      </TableContainer>
      <h1 style={{ padding: "30px 10px 10px 10px" }}>Charge Profil</h1>
      <TableContainer component={Paper}>
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Timestamp</TableCell>
              <TableCell align="right">Device ID</TableCell>
              <TableCell align="right">temperature</TableCell>
              <TableCell align="right">Consumed Power</TableCell>
              <TableCell align="right">Real forecast</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {charge_Profiles
              ? charge_Profiles.map((charge_Profile, index) => (
                  <TableRow key={charge_Profile.timestamp + index}>
                    <TableCell component="th" scope="row">
                      {charge_Profile.timestamp}
                    </TableCell>
                    <TableCell align="right">
                      {charge_Profile.values.Device_ID}
                    </TableCell>
                    <TableCell align="right">
                      {charge_Profile.values.Temperature}
                    </TableCell>
                    <TableCell align="right">
                      {charge_Profile.values.Consumed_Power}
                    </TableCell>
                    <TableCell align="right">
                      {charge_Profile.values.Real_Forecast ? "True" : "False"}
                    </TableCell>
                  </TableRow>
                ))
              : null}
          </TableBody>
        </Table>
      </TableContainer>
    </React.Fragment>
  );
}
export default Donnees;
